          role="tabpanel"
          aria-labelledby="justify-center-pills-details-tab"
        >
        <div class="row">
          <div class="col-md-3">
          <h4 class="mb-4 text-center">Project Details!</h4>
          <ul class="list-group list-group-flush mb-4">
            <li class="list-group-item">Project Short Name : {{ selectedProject.short_name }}</li>
            <li class="list-group-item">Project Full Name : {{ selectedProject.project_name }}</li>
            <li class="list-group-item">Contract Number : {{ selectedProject.contract_number }}</li>
            <li class="list-group-item">Financial Year : {{ selectedProject.financial_year }}</li>
            <li class="list-group-item">Project Cost : {{ selectedProject.project_cost }}</li>
            <li class="list-group-item">Client : {{ selectedProject.client }}</li>
            <li class="list-group-item">Start Date : {{ selectedProject.start_date }}</li>
            <li class="list-group-item">End Date : {{ selectedProject.end_date }}</li>
            <li class="list-group-item" >Project Progress :  {{project_progress }} %

                <div class="progress br-30 mt-2 " min="0" style="height : 30px">
<div
  role="progressbar"
  aria-valuemin="0"
  aria-valuemax="100"
  :aria-valuenow="project_progress"
  class="progress-bar"
  :class="progressColor"
  :style="{ width: project_progress + '%' }">


        <!-- <div class="progress-title "><span>Currently at Stone Pitching</span><span>90%</span></div> -->
        <div class="progress-title "><span></span><span> {{project_progress}}%</span></div>
      </div>
</div>

            </li>
          </ul>
