        >
          <!-- boq table starts -->
<div class="d-flex me-5">
  <div class="ms-auto">
                   <button v-if="selectedProject" class="btn btn-success me-2" @click="printBoq(selectedProject.id,selectedProject.project_name)">
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
  </svg>
    </button>
  </div>
</div>

         <div v-if="boq.length > 0 " style="max-height: 600px; overflow-y: auto;" >
  <table class="table table-bordered mt-4">
      <thead class="thead-light" style="position: sticky; top: 0; background: white; z-index: 5;">
              <tr>
                <th scope="col" class="text-dark">Code/SN</th>
                <th scope="col" class="text-dark">Description</th>
                <th scope="col" class="text-dark">Unit</th>
                <th scope="col" class="text-dark">Quantity</th>
                <th scope="col" class="text-dark">Rate</th>
                <th scope="col" class="text-dark">Total</th>
              </tr>
            </thead>
     <tbody >
        <tr >
    <td colspan="6" class="bg-dark text-white" >
      Bill of Quantity {{ selectedProject.project_name }}
    </td>
  </tr>
<template v-for="(series, i) in boq" :key="'series-' + i">
  <tr>
    <th colspan="1" class="text-center">SERIES {{ i + 1 }}</th>
    <th colspan="5" class="text-start">{{ series.name }}</th>
  </tr>

  <template v-for="(section, j) in series.sections" :key="'section-' + j">
    <tr>
      <th colspan="1" class="text-center">Section {{ i + 1 }}.{{ j + 1 }}</th>
      <th colspan="5" class="text-start">{{ section.name }}</th>
    </tr>

    <template v-for="(line, a) in section.lines" :key="'line-' + a">
      <tr>
        <td class="text-center">{{ String.fromCharCode(97 + a) }}</td>
        <td class="text-start">{{ line.name }}</td>
        <td>{{ line.unit }}</td>
        <td>{{ Number(line.quantity).toLocaleString() }}</td>
        <td class="text-end">{{  Number(line.rate).toLocaleString() }}</td>
        <td class="text-end">{{(line.rate * line.quantity).toLocaleString() }}</td>
      </tr>
    </template>

    <template v-for="(sub, k) in section.sub_sections" :key="'sub-' + k">
      <tr>
        <th colspan="1" class="text-center">{{ i + 1 }}.{{ j + 1 }}.{{ k + 1 }}</th>
        <th colspan="5" class="text-start">{{ sub.name }}</th>
      </tr>

      <template v-for="(line, b) in sub.lines" :key="'subline-' + b">
        <tr>
          <td class="text-center">{{ String.fromCharCode(97 + b) }}</td>
          <td class="text-start">{{ line.name }}</td>
          <td>{{ line.unit }}</td>
          <td>{{ Number(line.quantity).toLocaleString()  }}</td>
          <td class="text-end">{{ Number(line.rate).toLocaleString() }}</td>
          <td class="text-end">{{ (line.rate * line.quantity).toLocaleString() }}</td>
        </tr>
      </template>
    </template>
  </template>
</template>
  <div v-if="boq.lenght == 0">No Series Set for this project</div>
</tbody>

          </table>
        <div v-if="boq.length === 0 && selectedProject" class="text-center">No BoQ set for this project</div>
        <div class="mx-4">
  <h5 v-if="project" class="text-center bg-dark">Summary</h5>

  <div v-for="series in boq" class="row" :key="series.id">
    <div class="col-md-9 fw-bold">
      <p class="fw-bold">Series {{ series.name }}</p>
    </div>
    <div class="col-md-3 text-end">
      {{
        series.sections.reduce((sAcc, section) => {
          const sectionLinesTotal = section.lines.reduce((lAcc, line) => lAcc + line.quantity * line.rate, 0);
          const subSectionsTotal = section.sub_sections.reduce((ssAcc, sub) => 
            ssAcc + sub.lines.reduce((lAcc, line) => lAcc + line.quantity * line.rate, 0), 0
          );
          return sAcc + sectionLinesTotal + subSectionsTotal;
        }, 0).toLocaleString()
      }}
    </div>
  </div>

  <div class="row">
    <div class="col-md-9 fw-bold">
      <p class="fw-bold">A Total of Bills</p>
    </div>
    <div class="col-md-3 text-end">
      {{
        boq.reduce((totalAcc, series) => 
          totalAcc + series.sections.reduce((sAcc, section) => {
            const sectionLinesTotal = section.lines.reduce((lAcc, line) => lAcc + line.quantity * line.rate, 0);
            const subSectionsTotal = section.sub_sections.reduce((ssAcc, sub) => 
              ssAcc + sub.lines.reduce((lAcc, line) => lAcc + line.quantity * line.rate, 0), 0
            );
            return sAcc + sectionLinesTotal + subSectionsTotal;
          }, 0), 0
        ).toLocaleString()
      }}
    </div>
  </div>
</div>

           <!-- boq table ends -->
        </div>
        <div v-else>
          <span v-if="loading" v-html="loadingIndicator()"></span>
          <span v-else>
           No BOQ for this project!
          </span>
