      <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
        <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
        <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
      </svg>
    </button>
  </div>
</div>
          <!-- planning accordion starts -->
<div class="accordion accordion-flush" id="accordionFlushExample">
   <div v-for="budget in budgets" class="accordion-item">
    <h2 class="accordion-header">
      <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" :data-bs-target="`#budget-${budget.id}`" aria-expanded="false" aria-controls="flush-collapseOne">
        {{ budget.name }}  &nbsp;&nbsp;&nbsp; Budgeted: {{ formatNumber(budget.total_cost) }}  &nbsp;&nbsp;&nbsp;  Used: {{ formatNumber(budget.total_used_cost) }}  &nbsp;&nbsp;&nbsp; Balance: {{ formatNumber(budget.total_cost - budget.total_used_cost) }}
      </button>
    </h2>
    <div :id="`budget-${budget.id}`" class="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
      <div class="accordion-body">
       <div class="d-flex me-5 mb-2">
  <div class="ms-auto">
                   <button v-if="selectedProject" class="btn btn-outline-success me-2" @click="printBudget(budget.id, selectedProject.id)">
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
  </svg>
    </button>
  </div>
</div>
        <!-- Items Accordion -->
        <div class="accordion accordion-flush" :id="`itemsAccordion-${budget.id}`">
          <div v-for="(item, index) in budget.items" class="accordion-item" style="border:1px solid green">
            <h2 class="accordion-header">
              <button class="accordion-button collapsed" type="button" 
                      data-bs-toggle="collapse" 
                      :data-bs-target="`#item-${item.id}`" 
                      aria-expanded="false">
                {{ index + 1 }}. {{ item.name }} &nbsp;&nbsp;&nbsp; Budgeted: {{ formatNumber(item.total_budget_items_cost) }} &nbsp;&nbsp;&nbsp; Used: {{ formatNumber(item.total_used_cost) }} &nbsp;&nbsp;&nbsp; Balance: {{ formatNumber(Number(item.total_budget_items_cost) - Number(item.total_used_cost)) }}
              </button>
            </h2>
            <div :id="`item-${item.id}`" 
                 class="accordion-collapse collapse" 
                 :data-bs-parent="`#itemsAccordion-${budget.id}`">
              <div class="accordion-body">
                
                <!-- Descriptions v-client-table -->
                 <div class="panel p-0">
                <div class="custom-table">
                  <v-client-table :data="item.descriptions" :columns="descriptionsColumns" :options="table_option">
                    <template #Sno="props">
                      {{ props.index + 0 }}
                    </template>
                    <template #actions="props">
                      <a href="javascript:void(0);" 
                         title="View Details" 
                         data-bs-toggle="modal" 
                         data-bs-target="#view-description-modal"
                         @click="viewDescription(props.row);">
                        <svg xmlns="http://www.w3.org/2000/svg" 
                             width="24" 
                             height="24" 
                             viewBox="0 0 24 24" 
                             fill="none" 
                             stroke="currentColor" 
                             stroke-width="1.5" 
                             stroke-linecap="round" 
                             stroke-linejoin="round"
                             class="text-primary">
                          <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path>
                          <circle cx="12" cy="12" r="3"></circle>
                        </svg>
                      </a>
                    </template>
                    <template #rate="props">
                      {{ formatNumber(props.row.cost) }}
                    </template>
                    <template #cost="props">
                      {{ formatNumber(props.row.total_budget_item_description_cost) }}
                    </template>
                    <template #used_cost="props">
                      {{ formatNumber(props.row.used_cost) }}
                    </template>
                    <template #balance="props">
                      {{ formatNumber(props.row.total_budget_item_description_cost - parseFloat(props.row.used_cost || 0)) }}
                    </template>
                  </v-client-table>
                </div>
                </div>
                
              </div>
            </div>
          </div>
        </div>
        
      </div>
    </div>
  </div>
     <div class="accordion-item">
    <h2 class="accordion-header">
      <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse" :data-bs-target="`#unbudgeted`" aria-expanded="false" aria-controls="flush-collapseOne">
