      <div class="accordion-body">
            
                    <div class="custom-table">


                         <v-client-table :data="unbudgetedItems" :columns="columns" :options="table_option" >
                            <template #actions="props">
                            <button 
                          v-if="user_permissions.includes('can_add_budget_consumption')"
