        <!-- PROVISIONAL IPC -->
         
           <div
          class="tab-pane fade"
          id="ipc"
          role="tabpanel"
          aria-labelledby="ipc-tab"
        >
        <div class="d-flex me-5">
  <div class="ms-auto">
                   <button v-if="selectedProject" class="btn btn-success me-2" @click="printIpc(selectedProject.id,selectedProject.project_name)">
  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
