<div class="table-responsive"  style="max-height: 600px; overflow-y: auto;">
          <table class="table table-bordered mt-4">
      <thead class="thead-light" style="position: sticky; top: 0; background: white; z-index: 5;">
                      <tr>
                <th  colspan="6" class="text-dark text-center ">Original BoQ</th>
                <th colspan="2" class="text-dark text-nowrap text-center ">Previous IPC</th>
                <th colspan="2"  class="text-dark text-nowrap text-center" style="background-color: beige;border: 1px solid black;">This IPC</th>
                <th colspan="2"  class="text-dark text-nowrap text-center">Remaining Balance</th>
              </tr>
        <tr>
                <th scope="col" class="text-dark">Code/SN</th>
                <th scope="col" class="text-dark">Description</th>
                <th scope="col" class="text-dark">Unit</th>
                <th scope="col" class="text-dark">Qty</th>
                <th scope="col" class="text-dark">Rate</th>
                <th scope="col" class="text-dark">Total</th>
                <!-- previous ipc -->
                   <th scope="col" class="text-dark">Qty</th>
                <th scope="col" class="text-dark">Total</th>
                <!-- this ipc -->
                   <th scope="col" class="text-dark" style="background-color: beige;border: 1px solid black;">Qty</th>
                <th scope="col" class="text-dark" style="background-color: beige;border: 1px solid black;">Total</th>
                <!-- balance -->
                   <th scope="col" class="text-dark">Qty</th>
                <th scope="col" class="text-dark">Total</th>
              </tr>
            </thead>
     <tbody >
<template v-for="(series, i) in boq" :key="'series-' + i">
  <tr>
    <th colspan="1" class="text-center">SERIES {{ i + 1 }}</th>
    <th colspan="5" class="text-start">{{ series.name }}</th>
  </tr>

  <template v-for="(section, j) in series.sections" :key="'section-' + j">
    <tr>
      <th colspan="1" class="text-center">Section {{ i + 1 }}.{{ j + 1 }}</th>
      <th colspan="5" class="text-start">{{ section.name }}</th>
    </tr>

    <template v-for="(line, a) in section.lines" :key="'line-' + a">
      <tr >
        <td class="text-center">{{ String.fromCharCode(97 + a) }}</td>
        <td class="text-start">{{ line.name }}</td>
        <td>{{ line.unit }}</td>
        <td>{{  Number(line.quantity).toLocaleString() }}</td>
        <td class="text-end">{{  Number(line.rate).toLocaleString() }}</td>
        <td class="text-end">{{(line.rate * line.quantity).toLocaleString() }}</td>
                  <!-- previous ipc -->
          <td @click="showError(`You Can't edit previous IPC`)">{{ line?.previous_progress?.quantity ?? 0 }}</td>
           <td>{{ ((line?.previous_progress?.quantity ?? 0)* line.rate).toLocaleString() }}</td>
          <!-- this ipc -->
            <td style="cursor: pointer;background-color: beige;border: 3px solid white;" @click="fillEdit(line)" >
            <span >
             {{ Number(line?.progress?.quantity ?? 0).toLocaleString() }} 
            </span>
            </td>
          <!-- remaining balance -->
          <td>{{ formatNumber(((line?.progress?.quantity ?? 0) * line.rate))  }} </td>
             <td>{{ (Number(line.quantity) - (Number(line?.progress?.quantity ?? 0) +  Number(line?.previous_progress?.quantity ?? 0))).toFixed(2)  }} </td>
          <td>{{ ((line.quantity - (Number(line?.progress?.quantity ?? 0) +  Number(line?.previous_progress?.quantity ?? 0))) * line.rate).toLocaleString() }}</td>
      </tr>
    </template>

    <template v-for="(sub, k) in section.sub_sections" :key="'sub-' + k">
      <tr>
        <th colspan="1" class="text-center">{{ i + 1 }}.{{ j + 1 }}.{{ k + 1 }}</th>
        <th colspan="5" class="text-start">{{ sub?.name }}</th>
      </tr>

      <template v-for="(line, b) in sub.lines" :key="'subline-' + b">
        <tr>
          <td class="text-center">{{ String.fromCharCode(97 + b) }}</td>
          <td class="text-start">{{ line?.name }}</td>
          <td>{{ line.unit }}</td>
          <td>{{ line.quantity }}</td>
          <td class="text-end">{{ line.rate.toLocaleString() }}</td>
          <td class="text-end">{{ (line.rate * line.quantity).toLocaleString() }}</td>
          <!-- previous ipc -->
          <td>{{ line?.progress?.quantity ?? 0 }}</td>
          <td>{{ ((line?.progress?.quantity ?? 0)* line.rate).toLocaleString() }}</td>
          <!-- this ipc -->
          <td  style="cursor: pointer;background-color: beige;border: 3px solid white;"
          >
            <span >
             {{ line?.progress?.quantity ?? 0 }}
            </span>
          </td>
          <td>{{ ((line?.progress?.quantity ?? 0)* line.rate).toLocaleString() }}</td>
            <!-- remaining balance -->
          <td>{{ (line.quantity - ((line?.progress?.quantity ?? 0) +  (line?.previous_progress?.quantity ?? 0)))  }}</td>
          <td>{{ ((line.quantity - ((line?.progress?.quantity ?? 0) +  (line?.previous_progress?.quantity ?? 0))) * line.rate).toLocaleString() }}</td>
        </tr>
      </template>
    </template>
  </template>
</template>
  <div v-if="boq.lenght == 0">No Series Set for this project</div>
</tbody>
  <tfoot class="thead-light" style="position: sticky; top: 0; background: white; z-index: 5;">
        <tr>
                <th colspan="5" class="text-dark text-end">GRAND TOTAL</th>
                <th scope="col" class="text-dark">
      {{
        boq.reduce((totalAcc, series) => 
          totalAcc + series.sections.reduce((sAcc, section) => {
            const sectionLinesTotal = section.lines.reduce((lAcc, line) => lAcc + line.quantity * line.rate, 0);
            const subSectionsTotal = section.sub_sections.reduce((ssAcc, sub) => 
              ssAcc + sub.lines.reduce((lAcc, line) => lAcc + line.quantity * line.rate, 0), 0
            );
            return sAcc + sectionLinesTotal + subSectionsTotal;
          }, 0), 0
        ).toLocaleString()
      }}
                </th>
                <th scope="col" class="text-dark">&nbsp;</th>
                <th scope="col" class="text-dark">
{{
  boq.reduce((t, s) =>
    t + s.sections.reduce((st, sec) =>
      st
      + sec.lines.reduce((l, ln) => l + ((ln?.previous_progress?.quantity ?? 0) * ln.rate), 0)
      + sec.sub_sections.reduce((ss, sub) =>
        ss + sub.lines.reduce((l, ln) => l + ((ln?.previous_progress?.quantity ?? 0) * ln.rate), 0)
      , 0)
    , 0)
  , 0).toLocaleString()
}}
</th>

                <th scope="col" class="text-dark">&nbsp;</th>
              <th scope="col" class="text-dark">
{{
 formatNumber( boq.reduce((t, s) =>
    t + s.sections.reduce((st, sec) =>
      st
      + sec.lines.reduce((l, ln) => l + ((ln?.progress?.quantity ?? 0) * ln.rate), 0)
      + sec.sub_sections.reduce((ss, sub) =>
        ss + sub.lines.reduce((l, ln) => l + ((ln?.progress?.quantity ?? 0) * ln.rate), 0)
      , 0)
    , 0)
  , 0))
}} 
</th>

                <th scope="col" class="text-dark">&nbsp;</th>
<th scope="col" class="text-dark">
{{
  boq.reduce((t, s) =>
    t + s.sections.reduce((st, sec) =>
      st
      + sec.lines.reduce((l, ln) => {
        const qty = Number(ln.quantity) || 0
        const rate = Number(ln.rate) || 0
        const prev = Number(ln?.previous_progress?.quantity) || 0
        const curr = Number(ln?.progress?.quantity) || 0
        return l + ((qty - (prev + curr)) * rate)
      }, 0)
      + sec.sub_sections.reduce((ss, sub) =>
        ss + sub.lines.reduce((l, ln) => {
          const qty = Number(ln.quantity) || 0
          const rate = Number(ln.rate) || 0
          const prev = Number(ln?.previous_progress?.quantity) || 0
          const curr = Number(ln?.progress?.quantity) || 0
          return l + ((qty - (prev + curr)) * rate)
        }, 0)
      , 0)
    , 0)
  , 0).toLocaleString()
}} 
</th>


              </tr>
            </tfoot>
          </table>
</div>

        <div v-if="boq.length === 0 && selectedProject" class="text-center">No BoQ set for this project</div>
        </div>

        <!-- SITE ACTIVITIES -->
 
        <div
          class="tab-pane fade"
          id="activities"
          role="tabpanel"
          aria-labelledby="activities-tab"
        >
  <div class="panel p-0 mt-3">
    <!-- <h5 class="text-center mt-3">SITE WORK REPORT </h5> -->
  <div v-if="loading" v-html="waitIndicator()"></div>
  <div class="text-center" v-if="siteWorkReport.length == 0 && !loading">No Report Made for this site</div>
    <!-- Add New Button -->
    <div class="d-flex me-5">
      <div class="ms-auto d-flex gap-5">
        <input 
          type="text" 
          v-model="searchQuery" 
          class="form-control form-control-sm" 
          style="width: 250px;"
          placeholder="Search work carried out..."
        >
      </div>
