        class="col-12 col-md-6 mb-4"
      >
        <div class="card shadow">
          <div class="card-body">
            <div class="d-flex justify-content-between align-items-center">
              <h5 class="card-title text-dark fw-bold text-start mb-0">
                {{ report?.report_name || 'Untitled Report' }}{{ report?.description ? `(${report?.description})` : '' }} {{ report?.date }}
              </h5>
            </div>



            <div class = "my-4">
            <p class="text-muted mb-1 text-start"><strong>Supervisor:</strong> {{ report.supervisor?.name }}</p>
           <p v-if="report.remark" class="text-muted mb-1 text-start mt-3"><strong>Remark:</strong> {{ report.remark }}</p>

            <div class="panel p-0 mt-3">
         <div class="custom-table table3 mt-3">
  <v-client-table 
    :data="report.activities" 
    :columns="activity_columns" 
    :options="table_option">
  </v-client-table>
</div>
            </div>

</div>


<!-- Attachments Section -->
<div class="row mt-3" v-if="report.attachments && report.attachments.length > 0">
  <div class="col-12">
    <h6 class="text-start fw-bold mb-3">Activity Attachments</h6>
    <div class="row g-3">
      <div 
  v-for="attachment in report.attachments" 
  :key="attachment.id"
  :class="report.attachments.length > 6 ? 'col-md-3 col-sm-6' : report.attachments.length > 4 ? 'col-md-4 col-sm-6' : 'col-md-6 col-sm-6'"
>
        <div class="card shadow-sm h-100">
  <div class="card-body p-2">
    <div class="attachment-preview mb-2" style="height: 150px; overflow: hidden; border-radius: 9px; display: flex; align-items: center; justify-content: center; background-color: #f8f9fa;">
      <img 
        :src="`${baseURL}/${attachment.file_path}`" 
        :alt="attachment.description"
        style="max-width: 100%; max-height: 100%; object-fit: contain; cursor: pointer;"
        @click="viewAttachment(`${baseURL}/${attachment.file_path}`, attachment)"
        data-bs-toggle="modal" 
        data-bs-target="#view-attachment-modal"
      >
    </div>
    <div class="d-flex justify-content-between align-items-center">
      <p class="mb-1 small text-muted">
        <strong>{{ attachment.description || ' ' }}</strong>
      </p>
      
    </div>
  </div>
</div>
      </div>
    </div>
  </div>
</div>


            <!-- Footer Actions -->
<div class="card-footer bg-transparent border-0 d-flex justify-content-between">
  
    </div>

          </div>
        </div>
      </div>
    </div>

  
</div>
        </div>

         <!-- analysis starts -->
                   <div
          class="tab-pane fade"
          id="analysis"
          role="tabpanel"
          aria-labelledby="analysis-tab"
        >
        <div class="d-flex flex-column align-items-center mb-4">
        <nav>
          <div class="nav nav-tabs " id="nav-tab" role="tablist">
      <button
