<div class="modal fade mymodal" id="view-attachment-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="attachmentModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-fullscreen">
