<div class="modal fade mymodal" id="assign-budget-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="assignBudgetModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-xl">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="assignBudgetModalLabel">Assign Budget for Ref no.: {{ requisition_item.approval_id }}</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close" @click="closeEditModal()"></button>
      </div>
      <form @submit.prevent="assignBudget()">
        <div class="modal-body">


            <!-- Item Info -->
            <div class="mb-4" v-if="requisition_item">
              <h5 class="mb-3">Item Information</h5>
              <div class="row">
                <div class="col-md-4 mt-3">
                  <p class="mb-3" style="font-size: 1.05rem;"><strong>Item:</strong> {{ requisition_item.item || 'N/A' }}</p>
                 <p class="mb-3" style="font-size: 1.05rem;"><strong>Cost:</strong> {{ requisition_item.cost || 'N/A' }}</p>
                </div>
                <div class="col-md-4 mt-3">
                  <p class="mb-3" style="font-size: 1.05rem;"><strong>Quantity:</strong> {{ requisition_item.quantity || 'N/A' }}</p>
                  <p class="mb-3" style="font-size: 1.05rem;"><strong>Description:</strong> {{ requisition_item.description || '--' }}</p>
                </div>
                <div class="col-md-4 mt-3">
                  <p class="mb-3" style="font-size: 1.05rem;"><strong>Rate:</strong> {{ requisition_item.rate || 'N/A' }}</p>
                </div>
              </div>
            </div>


          
            <div class="row mb-3">
              <div class="col-md-6">
                  <label class="form-label small">Select A Budget</label>
                  <multiselect
                    v-model="budget"
                    :options="budgets"
                    placeholder="Select A Budget"
                    :searchable="true"
                    label="name"
                    :allow-empty="false"
                  />
                </div>

                <div class="col-md-6" v-if="budget">
                  <label class="form-label small">Select An Item</label>
                  <multiselect
                    v-model="budget_item"
                    :options="budgetItems"
                    placeholder="Select An Item"
                    :searchable="true"
                    label="name"
                    :allow-empty="false"
                  />
                </div>

                <div class="col-md-12" v-if="budget_item">
                  <label class="form-label small">Select Description</label>
                  <multiselect
                    v-model="budget_item_description"
                    :options="budgetItemDescriptions"
                    placeholder="Select An Option"
                    :searchable="true"
                    label="name"
                    :allow-empty="false"
                  />
                </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="closeEditModal()" id="close_assign_budget">Close</button>
          <button type="submit" class="btn btn-primary" :disabled="Assigning">
            <span v-if="Assigning" class="spinner-border spinner-border-sm text-white me-2" role="status"></span>
            Assign
          </button>
        </div>
      </form>
    </div>
  </div>
</div>
    <!-- assign budget modal ends -->

<!-- view budget item description consumption modal starts -->
<div class="modal fade mymodal" id="view-description-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="viewDescriptionModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-fullscreen">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="viewDescriptionModalLabel">
          Budget Item Description Consumption for {{ selectedDescription?.name || 'N/A' }}
        </h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
        <div class="modal-body">
          <!-- Budget and Project Information -->
          <table class="table table-bordered table-responsive mb-3">
            <thead>
              <tr>
                <th colspan="8" class="bg-light text-center text-dark fs-6">Budget Details</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th>Project:</th>
                <td>{{ selectedProject?.short_name || '--' }}</td>
                <th>Budget Name:</th>
                <td>{{ selectedDescription?.budget_item?.budget?.name || '--' }}</td>
                <th>Budget Item:</th>
                <td>{{ selectedDescription?.budget_item?.name || '--' }}</td>
                <th>Description:</th>
                <td>{{ selectedDescription?.name || '--' }}</td>
              </tr>
             
              <tr>
                <th>Unit:</th>
                <td>{{ selectedDescription?.units || '--' }}</td>
                <th>Quantity:</th>
                <td>{{ formatNumber(selectedDescription?.quantity) || '--' }}</td>
                <th>Rate:</th>
                <td>{{ formatNumber(selectedDescription?.cost) || '--' }} </td>
                <th>Total Cost:</th>
                <td>{{ formatNumber(selectedDescription?.total_budget_item_description_cost) || '--' }} </td>
              </tr>
              <tr>
                <th>Used Quantity:</th>
                <td>{{ selectedDescription?.cost && selectedDescription?.used_cost ? (selectedDescription.used_cost / selectedDescription.cost).toFixed(2) : '--' }}</td>
                <th>Used Cost:</th>
                <td>{{ formatNumber(selectedDescription?.used_cost) || '--' }} </td>
                <th>Balance:</th>
                <td colspan="3">{{ formatNumber((selectedDescription?.total_budget_item_description_cost || 0) - (selectedDescription?.used_cost || 0)) || '--' }} </td>
              </tr>
            </tbody>
          </table>
          <div class="panel p-0">
                    <div class="custom-table table3">
                      <div v-if="loading_spinner" v-html="tableShimmer()"></div>
                        <v-client-table v-else :data="consumptions" :columns="consumptionsColumns" :options="table_option">
                            <template #actions="props">
                             <router-link :to="`/approval/${props.row.approval_id}`">
                          <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-arrow-right" style="cursor: pointer;" viewBox="0 0 16 16">
  <path fill-rule="evenodd" d="M1 8a.5.5 0 0 1 .5-.5h11.793l-3.147-3.146a.5.5 0 0 1 .708-.708l4 4a.5.5 0 0 1 0 .708l-4 4a.5.5 0 0 1-.708-.708L13.293 8.5H1.5A.5.5 0 0 1 1 8"/>
</svg>
                                </router-link>
                            </template>
                        </v-client-table>
                    </div>
                </div>
        </div>
        <div class="modal-footer">
