        const filteredSiteWorkReport = computed(() => {
  if (!searchQuery.value) {
    return siteWorkReport.value;
  }
  return siteWorkReport.value.filter(report => {
    const query = searchQuery.value.toLowerCase();
    return (
      report.report_name?.toLowerCase().includes(query) ||
      report.description?.toLowerCase().includes(query) ||
      report.supervisor?.name?.toLowerCase().includes(query) ||
      report.date?.includes(query) ||
      report.remark?.toLowerCase().includes(query) ||
      report.activities?.some(activity => 
        activity.activity?.toLowerCase().includes(query) ||
        activity.unit?.short_name?.toLowerCase().includes(query)
      )
    );
  });
});


