const selectedCategory = ref(0)
const costs = ref([])
const cost_columns = ref(['Sno','item','quantity','rate','cost','used_cost','actions'])
const getCosts = (cost_category_id) => {
  cost_category_id = Number(cost_category_id)

  costs.value = budgets.value.flatMap(budget =>
    budget.items.flatMap(item =>
      item.descriptions
        .filter(desc => cost_category_id === 0 || desc.cost_category_id === cost_category_id)
        .map((desc, index) => ({
          ...desc,
          Sno: index + 1,
          item: desc.name,
          quantity: (desc.quantity * desc.quantity_2).toLocaleString(),
          rate: formatNumber(desc.cost),
          cost: formatNumber(desc.quantity * desc.quantity_2 * desc.cost),
          used_cost : formatNumber(desc.used_cost)
        }))
    )
  )
}

const project_progress = ref(0)
const getProjectProgress = async () => {
  const response = await axiosInstance.get(`project-progress?project_id=${selectedProject.value.id}`)
  project_progress.value = response.data.overall_progress_percentage

  const p = Number(project_progress.value)
  if (p < 25) progressColor.value = 'bg-danger'
  else if (p < 50) progressColor.value = 'bg-warning'
  else if (p < 75) progressColor.value = 'bg-infoa'
  else progressColor.value = 'bg-success'

  // Set chart data
  const seriesNames = response.data.series.map(item => item.series_name)
  const seriesData = response.data.series.map(item => Number(item.progress_percentage.toFixed(2)))

  options_1.value = {
    ...options_1.value,
    xaxis: { categories: seriesNames }
  }

  series_1.value = [
    {
      name: 'BOQ Series',
      data: seriesData
    }
  ]
}


